				"html": "<h2>Heroku:</h2>\n<p>Once the person who will receive the Heroku app has created a Heroku account, follow the steps located here to transfer it to them: <a href=\"https://devcenter.heroku.com/articles/transferring-apps\">https://devcenter.heroku.com/articles/transferring-apps</a></p>\n<h2>MLAB:</h2>\n<p>Create an MLAB \"user\" account for the person whom you want to transfer your MLAB database: <a href=\"http://docs.mlab.com/accounts/#account-users\">http://docs.mlab.com/accounts/#account-users</a></p>\n<p>Then they would need to reassign your admin privileges to the account you've just created: <a href=\"http://docs.mlab.com/accounts/#re-assign-admin-privileges-admin-only\">http://docs.mlab.com/accounts/#re-assign-admin-privileges-admin-only</a></p>\n<h2>GitHub or BitBucket</h2>\n<p>The new project owners can either fork your existing repo or you can transfer ownership of it to them in GitHub: <a href=\"https://help.github.com/articles/about-repository-transfers/\">https://help.github.com/articles/about-repository-transfers/</a></p>\n<p>To transfer it in BitBucket: <a href=\"https://confluence.atlassian.com/bitbucket/change-or-transfer-repository-ownership-289964397.html\">https://confluence.atlassian.com/bitbucket/change-or-transfer-repository-ownership-289964397.html</a></p>\n<h2>Ensure no keys remain in your Git history</h2>\n<p>If your project will be open source, be careful to remove any keys (they should never have been committed in the first place, but it's better late than never to remove them). Here's how to search through your code history for them:</p>\n<p>If you find a key somewhere in your repository, or discover that a sensitive file such as your .env was somehow committed at one point, you can purge it from your git history with BFG: <a href=\"https://help.github.com/articles/remove-sensitive-data/\">https://help.github.com/articles/remove-sensitive-data/</a></p>",
